export const minutesToArrivalFromDateTimeString = (datetimeString) => {
    const now = + new Date();
    const arrivalTime = + new Date(datetimeString);

    const difference = arrivalTime - now;

    let minutesDifference

    if (difference) {
        minutesDifference = (difference / 60000) | 0;
        if (minutesDifference < 1) {
            minutesDifference = 'Arr' // less than one minute, show Arriving
        }
    } else {
        minutesDifference = null // won't show in view
    }

    return minutesDifference
}