import { userTypes } from './user.types'


const INITIAL_STATE = {
    userPreferences: null
}


const userReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case userTypes.SET_PREFERENCES:
            return {
                ...state,
                userPreferences: action.payload
            }

        default:
            return state
    }
}


export default userReducer