import React from 'react'
import './search-form.styles.css'

const SearchForm = ({ searchTerm, handleChange, handleSubmit }) => {

    return (
        <form className="search-form" onSubmit={handleSubmit}>
            <div className="form-group">
                <input
                    className="form-control"
                    type="text"
                    id="searchTerm"
                    placeholder="a"
                    onChange={handleChange}
                    value={searchTerm}
                />
                <label className="form-label" htmlFor="searchTerm">Bus stop code, road name or description</label>
            </div>

            <button className="orange ripple">Search</button>
        </form>
    )
}

export default SearchForm