export const getBusStopsNearMe = ({ longitude, latitude, radius }) => {
    const RADIUS = radius || 1

    const withinCertainDistance = allBusStops.reduce((acc, busStop) => {
        const distance = _getDistanceFromLatLonInKm(latitude, longitude, busStop.Latitude, busStop.Longitude)
        if (distance <= RADIUS) {
            return [...acc, ({ ...busStop, distance })]
        }

        return acc
    }, [])


    return withinCertainDistance
}


export const getBusStopsBasedOnSearchCriteria = (searchTerm) => {
    const filterCriteria = (busStopInfo => {
        const criterias = ['BusStopCode', 'RoadName', 'Description']

        return criterias.reduce((acc, criteria) => {
            return acc || busStopInfo[criteria].toLowerCase().includes(searchTerm.toLowerCase())
        }, false)
    })

    return allBusStops
        .filter(filterCriteria)
}


export const getBusStopInfo = (busStopCode) => {
    const busStop = allBusStops.find(busStop => busStop.BusStopCode === busStopCode)

    return busStop
}