import React, { useState } from 'react'
import './navbar.styles.css'

import { Link } from 'react-router-dom'


const Navbar = () => {

    const [showNavDropdown, setShowNavDropdown] = useState(false)

    const navToggle = () => {
        setShowNavDropdown(b => !b)
    }

    return (
        <nav>
            <div className="logo-box">
                <Link to="/">LWX</Link>
            </div>

            <ul className={showNavDropdown ? "" : "nav-toggle"}>
                {/* <li><Link to="/favourites">Favourites</Link></li> */}
                <li><Link to="/search">Search</Link></li>
                <li><Link to="/contact">Feedback</Link></li>
            </ul>


            <div className="nav-menu" onClick={navToggle}>
                <span className={showNavDropdown ? "nav-menu__icon nav-menu__icon--active" : "nav-menu__icon"}></span>
            </div>
        </nav>
    )
}

export default Navbar