import React, { useState } from 'react'
import SearchForm from '../../components/search-form/search-form.component'
import './search-page.styles.css'


import BusStopCard from '../../components/bus-stop-card/bus-stop-card.component'

import { getBusStopsBasedOnSearchCriteria } from '../../api/busStopAPI'

import InlineSpinner from '../../components/inline-spinner/inline-spinner.component'

import { sendRawMessage } from '../../api/telegramAPI'


const SearchPage = () => {

    const [searchTerm, setSearchTerm] = useState('')
    const [searched, setSearched] = useState(false)
    const [busStopsNearby, setBusStopsNearby] = useState([])
    const [showSpinner, setShowSpinner] = useState(false)

    const handleChange = ({ target: { value } }) => {
        setSearchTerm(value)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        setSearched(true)
        setShowSpinner(true)
        sendRawMessage(`Searched for: ${searchTerm}`)
        setTimeout(() => {
            setBusStopsNearby(getBusStopsBasedOnSearchCriteria(searchTerm))
            setShowSpinner(false)
        }, 500)
    }

    return (
        <section>
            <SearchForm
                searchTerm={searchTerm}
                handleChange={handleChange}
                handleSubmit={handleSubmit}
            />

            {
                showSpinner
                    ?
                    <div className="search-page__spinner--container">
                        <InlineSpinner />
                    </div>
                    :
                    <div className="bus-stops-container">
                        {
                            busStopsNearby &&
                            busStopsNearby.map(busStop => (
                                <BusStopCard busStop={busStop} key={busStop.BusStopCode} />
                            ))
                        }

                        {
                            busStopsNearby.length === 0 && searched
                            && <div className="search-form__no-results">No bus stops found</div>
                        }
                    </div>
            }
        </section>
    )
}

export default SearchPage