import React, { useState } from 'react'
import './bus-stop-card.styles.css'

import { withRouter } from 'react-router-dom'

import ArrivalCard from '../arrival-card/arrival-card.component'
import { getBusArrivals } from '../../api/busStopAPI'

import InlineSpinner from '../inline-spinner/inline-spinner.component'


const BusStopCard = ({ busStop }) => {

    const [toggled, setToggled] = useState(false)

    const [busStopInfo, setBusStopInfo] = useState(null)

    const [refreshInProgress, setRefreshInProgress] = useState(false)

    const setBusStopInfoIntoStore = () => {
        getBusArrivals(busStop.BusStopCode)
            .then(resp => resp.json())
            .then(resp => {
                setBusStopInfo(resp)
                setRefreshInProgress(false)
            })
            .catch(error => console.log('error -->', error))
    }

    const handleClick = () => {
        setToggled(b => !b)

        if (!busStopInfo) {
            setBusStopInfoIntoStore()
        }
    }


    const handleRefresh = () => {
        setBusStopInfo(null)
        setRefreshInProgress(true)
        setBusStopInfoIntoStore()
    }

    return (
        <div className="bus-stop-card-container">
            <div className="bus-stop-card__controls">
                <div className="bus-stop-card" onClick={handleClick}>
                    <h1>{busStop.BusStopCode}</h1>
                    <h3>{busStop.Description}</h3>
                    <h4>{busStop.RoadName}</h4>
                </div>

                <div className="filler-box" onClick={handleClick}></div>
                <span className={refreshInProgress ? 'refresh-icon spin' : 'refresh-icon'} onClick={handleRefresh}>&#10227;</span>

            </div>
            {
                toggled &&
                (
                    busStopInfo
                        ? <ArrivalCard busArrivals={busStopInfo} />
                        :
                        <div className="center mg-md mt-lg">
                            <InlineSpinner />
                        </div>
                )
            }
        </div>
    )
}

export default withRouter(BusStopCard)