const BOT_URL = 'https://api.telegram.org/bot1195871523:AAGHoe3AjjjXyJgl17pmFpix48bQ0Ev0_bY'


export const sendMessage = (message) => {
    const chat_id = '-267746164'

    const { name, freeText, category } = message

    const _message = `Name: ${name}\nCategory: ${category || 'Blank'}\nContent: ${freeText}\n\n`

    return fetch(`${BOT_URL}/sendMessage`, {
        method: 'POST',
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify({
            chat_id,
            text: _message
        })
    })
}

export const sendRawMessage = (message) => {
    const chat_id = '-267746164'

    return fetch(`${BOT_URL}/sendMessage`, {
        method: 'POST',
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify({
            chat_id,
            text: message
        })
    })
}