import React from 'react'
import './next-bus-display.styles.css'

import WaitTimeBar from '../wait-time-bar/wait-time-bar.component'
import { minutesToArrivalFromDateTimeString } from '../../utils/datetime-utils'


const NextBusDisplay = ({ nextBus }) => {

    console.log("busInfo -->")
    console.log(JSON.stringify(nextBus, null, 4))


    const minutesToArrival = minutesToArrivalFromDateTimeString(nextBus.EstimatedArrival)

    const loadToDisplay = (load) => {
        switch (load) {
            case 'SEA':
                return <WaitTimeBar color="lightgreen" percentage={20} />
            case 'SDA':
                return <WaitTimeBar color="orange" percentage={50} />
            case 'LSD':
                return <WaitTimeBar color="red" percentage={80} />
            default:
                break
        }
    }

    return (
        <span className="next-bus">
            {
                minutesToArrival
                    ?
                    <React.Fragment>
                        <span className="next-bus__minutes">{minutesToArrival}</span>
                        {
                            loadToDisplay(nextBus.Load)
                        }
                    </React.Fragment>
                    :
                    <React.Fragment>
                        <span className="next-bus__empty">-</span>
                        <WaitTimeBar color="grey" percentage="100" />
                    </React.Fragment>
            }
        </span>
    )
}

export default NextBusDisplay