import React, { useState } from 'react'
import './arrival-card.styles.css'

import NextBusDisplay from '../next-bus-display/next-bus-display.component'
import OverlayDisplay from '../overlay-display/overlay-display.component'

import { getBusStopInfo } from '../../api/busStopAPI'


const nextBuses = [
    'NextBus',
    'NextBus2',
    'NextBus3'
]


const AdditionalInfoCard = ({ bus }) => {
    console.log("additional info bus: -->")
    console.log(JSON.stringify(bus, null, 4))

    /**
     * from, to
     * feature (WAB?)
     * SD/DD/bendy
     */
    const { OriginCode: fromBusStopCode, DestinationCode: toBusStopCode } = bus.NextBus

    const busNaming = [
        'First Bus',
        'Second Bus',
        'Third Bus'
    ]


    const busTypes = {
        SD: 'Single Deck',
        DD: 'Double Deck',
        BD: 'Bendy'
    }


    const busStopCodeToLocation = (code) => {
        const busStop = getBusStopInfo(code)
        return busStop.RoadName
    }

    return (
        <div className="arrival-card__additional-info-container">
            <h2>{bus.ServiceNo}</h2>
            <h3 className="arrival-card__additional-info--bus-stop-code">{busStopCodeToLocation(fromBusStopCode)}
                <p>To</p>
                {busStopCodeToLocation(toBusStopCode)}</h3>
            {
                nextBuses.map((nextBus, index) => {
                    const { Feature, Type } = bus[nextBus]

                    return (
                        <div className="arrival-card__additional-info" key={nextBus}>
                            <p className="arrival-card__additional-info--bus-name">{busNaming[index]}</p >
                            <p>{Feature === 'WAB' ? 'Wheelchair Accessible' : 'Wheelchair InAccessible'}</p>
                            <p>{busTypes[Type]}</p>
                        </div >
                    )
                })
            }
        </div >
    )
}


const ArrivalCard = ({ busArrivals }) => {
    console.log("busArrivals -->")
    console.log(JSON.stringify(busArrivals, null, 4))

    const [showOverlay, setShowOverlay] = useState(false)
    const [busNumber, setBusNumber] = useState(null)




    const handleClick = (number) => {
        setBusNumber(number);
        toggleOverlay();
    }


    const toggleOverlay = () => {
        setShowOverlay(b => !b)
    }

    return (
        <div className="arrival-card">
            {
                busArrivals.Services
                    .sort((a, b) => a.ServiceNo.replace(/\D/g, '') - b.ServiceNo.replace(/\D/g, ''))
                    .map(busArrival => (
                        <div className="arrivals-container" key={busArrival.ServiceNo}>
                            <span className="arrival-card__service-no" onClick={() => handleClick(busArrival.ServiceNo)}>
                                {busArrival.ServiceNo}
                            </span>

                            {
                                nextBuses.map(nextBus => (
                                    <NextBusDisplay key={nextBus} nextBus={busArrival[nextBus]} />
                                ))
                            }
                        </div>
                    ))
            }

            {
                showOverlay && <OverlayDisplay toggleDisplay={toggleOverlay}>
                    <AdditionalInfoCard bus={busArrivals.Services.find(service => service.ServiceNo === busNumber)} />
                </OverlayDisplay>
            }
        </div>
    )
}

export default ArrivalCard