import React, { useState } from 'react'
import './contact-page.styles.css'

import TextInput from '../../components/text-input/text-input.component'

import { sendMessage } from '../../api/telegramAPI'

import Spinner from '../../components/spinner/spinner.component'
import OverlayDisplay from '../../components/overlay-display/overlay-display.component'
import { Link } from 'react-router-dom'

const ContactPage = () => {

    const MAX_LENGTH = 200

    const [state, setState] = useState({
        name: '',
        category: '',
        freeText: ''
    })


    const [showSpinner, setShowSpinner] = useState(false)
    const [sentMessage, setSentMessage] = useState(false)


    const handleChange = ({ target: { value, name } }) => {
        setState(prevState => ({ ...prevState, [name]: value }))
    }

    const handleContentEditableChange = (e) => {
        const value = e.currentTarget.textContent
        if (value.length > MAX_LENGTH) {
            return;
        }
        handleChange({ target: { value, name: 'freeText' } })
    }


    const handleSubmit = (e) => {
        e.preventDefault();
        setShowSpinner(true)

        sendMessage(state)
            .then(resp => resp.json())
            .then(_ => {
                setSentMessage(true)
                setShowSpinner(false)
            })
            .catch(error => console.log('error -->', error))
    }

    const freeTextIndicator = () => {
        return MAX_LENGTH - state.freeText.length
    }

    return (
        showSpinner
            ?
            <Spinner />
            :
            sentMessage
                ?
                <OverlayDisplay>
                    <div className="contact-page__thank-you">
                        <h1>Thank you for the feedback!</h1>
                        <Link to="/">Return to main page</Link>
                    </div>
                </OverlayDisplay>
                :
                <section className="contact-page">

                    <form onSubmit={handleSubmit}>
                        <TextInput
                            value={state.name}
                            name="name"
                            onChange={handleChange}
                            label="Name (optional)"
                        />

                        <select onChange={handleChange} name="category">
                            <option disabled={true} selected value="user didn't select">Select a category</option>
                            <option value="General">General</option>
                            <option value="Bug">Report a bug</option>
                            <option value="Compliment">Compliment</option>
                        </select>

                        <div className="contact-page__free-text">
                            <div
                                contentEditable={true}
                                onInput={handleContentEditableChange}
                                name="freeText"
                            >
                            </div>
                            <span className="contact-page__free-text--char-left">{freeTextIndicator()} chars left</span>
                        </div>

                        <button className="ripple blue">Send</button>
                    </form>
                </section>
    )
}

export default ContactPage